.Footer {
	background: var( --hm-warm-grey );
	color: var( --hm-light-grey );
	padding: 2.5rem 0;
	border-top: 1rem solid var( --hm-red );
	font-size: 0.833333rem;
	line-height: 1.7;
}

.Footer a,
.Footer a:link,
.Footer a:visited {
	color: #fff;
	border-color: #fff;
}

.Footer .wrapper {
	display: -ms-flexbox;
	display: flex;
}

.Footer .wrapper > * {
	width: 22%;
	margin-right: 3%;
}

.Footer .wrapper > *:first-child {
	width: 27%;
	margin-right: 23%;
}

.Footer .Logo {
	color: var( --hm-red-light );
	width: 15em;
	margin-bottom: 1rem;
}

.Footer .NavMenu {
	color: inherit;
}

.Footer .NavMenu-title {
	color: inherit;
}

.Footer .NavMenu a {
	color: inherit;
	text-decoration: none;
	opacity: 0.8;
}

.Footer .NavMenu a:hover,
.Footer .NavMenu a:active {
	background: var( --hm-beige );
	color: var( --hm-warm-grey );
	opacity: 1;
}

@media (max-width: 800px) {
	.Footer .wrapper {
		-ms-flex-direction: column;
		    flex-direction: column;
		padding: 0 0.5em;
	}

	.Footer .wrapper > *,
	.Footer .wrapper > *:first-child {
		width: auto;
		margin: 0.5em 0;
	}
}



