.HomeHeader {
	min-height: 400px;
	position: relative;
	overflow: hidden;
	color: #fff;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	text-align: center;
	text-shadow: 0 0 2px #504C4C;
}

.HomeHeader_bg-image {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: -1;

	background-color: var( --hm-medium-grey );
	background-repeat: no-repeat;
	background-size: cover;
}

.HomeHeader_bg-image::after {
	display: block;
	content: " ";
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 0;

	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(rgba( 79, 70, 65, 0.6 )),
		to(rgba( 79, 70, 65, 0.7 ))
	);

	background: -webkit-linear-gradient(
		top,
		rgba( 79, 70, 65, 0.6 ),
		rgba( 79, 70, 65, 0.7 )
	);

	background: -o-linear-gradient(
		top,
		rgba( 79, 70, 65, 0.6 ),
		rgba( 79, 70, 65, 0.7 )
	);

	background: linear-gradient(
		to bottom,
		rgba( 79, 70, 65, 0.6 ),
		rgba( 79, 70, 65, 0.7 )
	);
}

.HomeHeader_title {
	font-size: 2.777777778rem;
	line-height: 3.3325rem;
}

.HomeHeader .Logo {
	vertical-align: middle;
	width: 90px;
}


